                  <path d="M9.16046 0.826705L9.16046 0.826706C11.9153 3.08865 13.4999 6.36211 13.4999 9.96446C13.4999 13.5636 11.9178 16.9068 9.1614 19.1719L9.16084 19.1724C8.89248 19.3922 8.56799 19.5 8.24485 19.5C7.82458 19.5 7.40012 19.3171 7.11423 18.9213C6.62144 18.304 6.71488 17.4031 7.3268 16.9005L7.32699 16.9003C9.41381 15.1879 10.6105 12.6261 10.6105 9.96446C10.6105 7.305 9.41507 4.77789 7.32788 3.0633L9.16046 0.826705ZM9.16046 0.826705L9.15969 0.826075M9.16046 0.826705L9.15969 0.826075M9.15969 0.826075C8.54398 0.322575 7.63489 0.411805 7.12771 1.02689L7.12771 1.02688M9.15969 0.826075L7.12771 1.02688M7.12771 1.02688L7.12658 1.02826M7.12771 1.02688L7.12658 1.02826M7.12658 1.02826C6.6211 1.64577 6.71102 2.55682 7.32774 3.06318L7.12658 1.02826Z" fill={ statusData.voiceLevel === 3 ? statusData.isTalking ? statusData.radioActive ? '#FAFD56' : '#56FDFD' : 'white' : '' } stroke="black" strokeOpacity="0.3"/>
